export function AppFooter({ state }) {
  const { navScreen } = state;
  const [myState, handleSubmit] = useForm("xwkjzgyl");
  const inputRef = useRef(null);

  var submit = false;
  if (myState.succeeded) {
    submit = true;
    inputRef.current.value = "";
  }

  return (
    <div className="hfD">
      <div className="hfDa">
        <div className="hfDaA">Subscribe to our newsletters</div>

        <form className="hfDaB" onSubmit={handleSubmit} method="POST">
          <input
            ref={inputRef}
            id="email"
            type="email"
            name="email"
            className="hfDaBa"
            placeholder="Email Address"
            required
          />
          <ValidationError
            prefix="Email"
            field="email"
            errors={myState.errors}
          />

          <button
            type="submit"
            className="hfDaBb"
            disabled={myState.submitting}
          >
            Submit
          </button>
        </form>
        {submit && (
          <div className="newsletterSubmit">Thank you for subscribing!</div>
        )}
      </div>
      <div className="hfDb">
        <div className="hfDbA">
          <div className="hfDbAa" onClick={() => navScreen("/")} />

          <div className="hfDbAb">
            <a href="/" className="hfDbAbA">
              Home
            </a>
          </div>

          <div className="hfDbAb">
            <a href="/whyImportent" className="hfDbAbA">
              About Us
            </a>
          </div>

          <div className="hfDbAb">
            <a href="/ieMoneyWorks" className="hfDbAbA">
              IE money
            </a>
          </div>

          <div className="hfDbAb">
            <a href="/" className="hfDbAbA">
              Privacy policy
            </a>
          </div>

          {/* <div>
            <div className="hfDbAb">
              <div className="hfDbAbA" onClick={() => navScreen("/")}>
                Home
              </div>
              <div className="hfDbAbB">&nbsp;</div>
              <div className="hfDbAbC">FAQ's</div>
            </div>
            <div className="hfDbAb">
              <div className="hfDbAbA">About Us</div>
              <div className="hfDbAbB">Risk Disclosure & Disclaimer</div>
              <div className="hfDbAbC">Terms of use</div>
            </div>
            <div className="hfDbAb">
              <div className="hfDbAbA">IE money</div>
              <div className="hfDbAbB">Resources</div>
              <div className="hfDbAbC">Privacy policy</div>
            </div>
          </div> */}
        </div>
        <div className="hfDbB">
          <div className="hfDbBa" onClick={() => {}} />
          <div
            className="hfDbBb"
            onClick={() => window.open("https://twitter.com/iemoneyhq")}
          />
          <div
            className="hfDbBc"
            onClick={() =>
              window.open("https://www.linkedin.com/company/impact-ecosystem/")
            }
          />
          <div
            className="hfDbBd"
            onClick={() => window.open("https://www.instagram.com/ie_money/")}
          />
          <div
            className="hfDbBe"
            onClick={() => window.open("https://impactecosystem.medium.com/")}
          />
          <div
            className="hfDbBf"
            onClick={() => window.open("https://www.youtube.com/")}
          />
        </div>
      </div>
    </div>
  );
}
